@import url(https://fonts.googleapis.com/css2?family=Hind+Siliguri:wght@600&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.wrapper-result {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
}
@-webkit-keyframes changePosition {
  from {
    -webkit-transform: translate(0, 5px);
            transform: translate(0, 5px);
  }

  to {
    -webkit-transform: translate(0, -15px);
            transform: translate(0, -15px);
  }
}
@keyframes changePosition {
  from {
    -webkit-transform: translate(0, 5px);
            transform: translate(0, 5px);
  }

  to {
    -webkit-transform: translate(0, -15px);
            transform: translate(0, -15px);
  }
}
.img-weather {
  position: absolute;
  top: 5px;
  left: 0px;
  -webkit-animation-duration: 2s;
          animation-duration: 2s;
  -webkit-animation-name: changePosition;
          animation-name: changePosition;
  -webkit-animation-iteration-count: infinite;
          animation-iteration-count: infinite;
  -webkit-animation-direction: alternate;
          animation-direction: alternate;
}
.weather-text-container {
  position: absolute;
  top: 40px;
  right: 10px;
  font-size: 24px;
  color: sandybrown;
}
hr {
  width: 100%;
  border-top: 1px solid white;
  opacity: 0.4;
}
#poster {
  max-width: 70%;
  color: red;
  font-size: 24px;
}
#movie-imdb,
#movie-title {
  color: tomato;
  font-size: 24px;
  margin: 8px 0px;
}
#loading-gif {
  max-width: 10%;
}
@media screen and (max-width: 768px) {
  .weather-text-container {
    top: 10px;
    font-size: 20px;
  }
}

* {
  padding: 0;
  margin: 0;
  box-sizing: border-box;
}
html,
body,
#root,
.wrapper {
  height: 100%;
  background: url(/static/media/bg.0d14cb44.jpg);
}
.wrapper {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}
.icon {
  font-size: 20px;
  display: flex;
  margin: 10px auto;
}
.fa-icon-success {
  color: green;
}
.fa-icon-error {
  color: red;
}
.btn {
  margin-top: 16px;
  color: white;
  transition: none;
  -webkit-appearance: button-bevel;
}
.form-select {
  padding: 15px 25px;
  margin-bottom: 15px;
}
.link-container {
  width: 100%;
}
.text-alert {
  color: red;
  text-align: center;
}

.page-not-found {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  background-color: black;
  color: white;
  font-family: "Hind Siliguri", sans-serif;
}
.page-not-found h1 {
  font-size: 10rem;
}
.page-not-found p {
  font-size: 8rem;
}
@media screen and (max-width: 960px) {
  .page-not-found h1 {
    font-size: 7rem;
  }
  .page-not-found p {
    font-size: 3rem;
  }
}

/* http://meyerweb.com/eric/tools/css/reset/ 
   v2.0 | 20110126
   License: none (public domain)
*/

html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  font: inherit;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: "";
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}

